<template>
  <div id="app">
    <todo-list v-bind:todos="todos"></todo-list>

    <create-todo @create-todo="addTodo"></create-todo>
  </div>
</template>

<script>
import swal from 'sweetalert';
import TodoList from './TodoList.vue'
import CreateTodo from './CreateTodo.vue'

export default {
  name: 'app',
  data () {
    return {
      todos: [{
        title: 'Todo A',
        project: 'Project A',
        done: false,
      }, {
        title: 'Todo B',
        project: 'Project B',
        done: true,
      }, {
        title: 'Todo C',
        project: 'Project C',
        done: false,
      }, {
        title: 'Todo D',
        project: 'Project D',
        done: false,
      }]
    }
  },
  components:{
    TodoList,
    CreateTodo,
  },
  methods: {
    addTodo(newTodo) {
      this.todos.push(newTodo);
      swal("Success!", "To-Do created!", 'success');
    }
  }
}
</script>

<style lang="scss">

</style>



