<template>
    <div>
        <!-- <ul>
            <li>Todo A</li>
            <li>Todo B</li>
            <li>Todo C</li>
        </ul> -->
        <p>Completed Tasks: {{ todos.filter(todo => { return todo.done === true }).length}}</p>
        <p>Pending Tasks: {{ todos.filter(todo => { return todo.done === false }).length}}</p>
        <todo v-on:complete-todo="completeTodo" v-on:delete-todo="deleteTodo" v-for="todo in todos" :todo.sync="todo"></todo>
    </div>
</template>


<script type="text/javacript">
    // import swal from 'sweetalert'
    import Todo from './Todo.vue'
    export default {
        props: ['todos'], 
        components: {
            Todo,
        },
        methods: {
            deleteTodo(todo) {
                swal({
                    title: "Are you sure?",
                    text: "Once deleted, you will not be able to recover this imaginary file!",
                    icon: "warning",
                    buttons: true,
                    dangerMode: true,
                    })
                    .then((willDelete) => {
                    if (willDelete) {
                        const todoIndex = this.todos.indexOf(todo);
                        this.todos.splice(todoIndex, 1);
                        swal('Deleted!', 'Your' + '\"' + todo.title + '\"' + ' has been deleted.', 'success');
                    } else {
                        //swal("Your imaginary file is safe!");
                    }
                });
                        
            },
            completeTodo(todo) {
                const todoIndex = this.todos.indexOf(doto);
                this.todos[todoIndex].done = true;
               
            },
        }
    };
</script>

<style>

</style>



